  letter-spacing: 0.5px;
}

body {
  font-family: "Montserrat", sans-serif;
  margin: 0;
  padding: 0;
  color: #ffff;
  text-shadow: 0 0 12px rgb(0 0 0 / 25%);
  letter-spacing: 0.5px;
}

body > #root > div {
  height: 100vh;
}

p{margin:0}.Card{background:#009688;border-radius:10px;border:2px solid rebeccapurple}.Card__img{text-align:center}.Card__types{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center}.Card__type{padding:5px 30px;margin:10px;border-radius:5px;color:#fff;text-transform:capitalize}.Card__info{margin-left:10px}.Card__data{padding-bottom:10px}.Card__data .title{font-weight:700}.sprite_img{width:100px;height:100px}.card-body-wrapper{margin:0 auto;width:100%}.card-body-wrapper .card-name{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;text-align:center;text-transform:capitalize;font-weight:800}.card-body-wrapper .card-name h4{color:#222;text-decoration:none;font-weight:500;margin:10px 0;text-transform:capitalize}.card-body-wrapper .card-name span{font-weight:300;margin:5px;color:#666}.card-body-wrapper .card-img{margin:20px 0}.card-body-wrapper .card-img .sprite-wrapper{width:100%}@media (max-width: 768px){.sprite_img{width:100%;height:auto}}

.nav-wrapper{background-color:lightgray;width:100%;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center}.nav-wrapper .logo-link{text-decoration:none;color:#fff;display:-webkit-flex;display:flex}.nav-wrapper .logo-link .logo-wrapper{display:-webkit-flex;display:flex}.nav-wrapper .logo-link .logo-img{width:100%;height:auto}.nav-wrapper .logo-link .logo-h2{letter-spacing:0.5px;font-weight:500}

body{font-family:"Montserrat", sans-serif;margin:0;padding:0}.grid-home{display:-webkit-flex;display:flex;-webkit-justify-content:space-around;justify-content:space-around;-webkit-align-items:center;align-items:center;-webkit-flex-wrap:wrap;flex-wrap:wrap}.grid-home .box-wrapper{width:10em;height:10em;padding:20px;margin:10px;background-color:#f1f1f1;border-radius:15px;transition:all 0.6s cubic-bezier(0.165, 0.84, 0.44, 1)}.grid-home .box-wrapper:hover{-webkit-transform:scale(1.05, 1.05);transform:scale(1.05, 1.05)}.grid-home .box-wrapper .link-title{text-decoration:none}@media (max-width: 992px){.box-wrapper{width:17%}}@media (max-width: 768px){.grid-home .box-wrapper{width:35%;height:unset}}@media (max-width: 767px){.grid-home .box-wrapper{width:80%}}

.container-wrapper{-webkit-justify-content:space-around;justify-content:space-around;-webkit-align-items:center;align-items:center;-webkit-flex-direction:column;flex-direction:column;width:100%;height:100%;box-shadow:inset 0px 0px 0px 1000px rgba(218,218,218,0.55)}.container-wrapper .backButton{display:-webkit-flex;display:flex;-webkit-justify-content:flex-start;justify-content:flex-start;margin-left:10px}.container-wrapper .item-bod{display:-webkit-flex;display:flex;width:100%;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.container-wrapper .item-bod .detail-bod{margin:1em;padding:1.5em 2em;border-radius:15px;width:50%}.container-wrapper .item-bod .detail-bod .detail-name{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center}.container-wrapper .item-bod .detail-bod .detail-name .box-name{text-align:start}.container-wrapper .item-bod .detail-bod .detail-name .box-name p{text-decoration:none;font-weight:600;font-size:2.5em;margin:3px 0;text-transform:capitalize}.container-wrapper .item-bod .detail-bod .detail-name .box-name span{font-weight:300;margin:5px}.container-wrapper .item-bod .detail-bod .front-sprite-wrapper{margin:4em 0}.container-wrapper .item-bod .detail-bod .front-sprite-wrapper img{width:50%;height:auto}.container-wrapper .item-bod .detail-bod .weight-data{display:-webkit-flex;display:flex;-webkit-justify-content:space-evenly;justify-content:space-evenly;-webkit-align-items:center;align-items:center;-webkit-flex-wrap:wrap;flex-wrap:wrap}.container-wrapper .item-bod .detail-bod .weight-data .table-wrapper .table-title{font-weight:300;margin:5px}.container-wrapper .item-bod .detail-bod .weight-data .table-wrapper .table-content{text-decoration:none;font-weight:500;margin:3px 0;text-transform:capitalize}.container-wrapper .item-bod .detail-bod .weight-data .table-wrapper p{display:inline-block}.container-wrapper .item-bod .statistics-bod{margin:1em;padding:1.5em 2em;border-radius:15px;width:25%;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.container-wrapper .item-bod .statistics-bod .description-wrapper h2{text-align:left;margin:10px 0}.container-wrapper .item-bod .statistics-bod .description-wrapper p{font-size:1.25em;font-weight:300;text-align:justify;margin:5px 0 10px}.container-wrapper .item-bod .statistics-bod .stat-box{background:linear-gradient(to bottom, #fff 0%, #f7f7f7 100%);padding-top:6px;padding-bottom:6px;padding-left:13px;padding-right:13px;margin-top:0;margin-bottom:14px;margin-right:10px;color:rgba(0,0,0,0.65);font-weight:600;letter-spacing:1.5px;border-radius:8px;display:inline-block;font-size:12px;text-transform:uppercase;box-shadow:rgba(149,157,165,0.2) 0px 8px 24px}@media (max-width: 768px){.container-wrapper{-webkit-justify-content:flex-start;justify-content:flex-start;height:unset}.container-wrapper .item-bod{-webkit-flex-direction:column;flex-direction:column}.container-wrapper .item-bod .detail-bod{width:unset}.container-wrapper .item-bod .detail-bod .front-sprite-wrapper img{width:100%}.container-wrapper .item-bod .statistics-bod{width:unset}}


